export default {

	data() {
		return {
		name:'',
		bank:'',
		bankplace:'',
		card:"",
		phone:uni.getStorageSync('phone'),
		region:'',
		userInfo:{}
		}
	},
	onLoad(e) {
		// this.phone = e.phone
		uni.request({
			url:this.$bashUrl+'/index.php/home/Api/user_is',
			method:"POST",
			data:{
				phone:this.phone
			},
			success:res=>{
				console.log(res)
				this.name = res.data.name
				this.bank = res.data.bank
				this.bankplace = res.data.address
				this.card = res.data.card_nu
				this.region = res.data.region
			}
		})
	},
	methods: {
		save(){
			console.log('save')
			uni.request({
				url:this.$bashUrl+'/index.php/home/Api/user_is',
				method:"post",
				data:{
					name:this.name,
					card_nu:this.card,
					bank:this.bank,
					address:this.bankplace,
					phone:this.phone,
					region:this.region
				},
				success:res=>{
					console.log(res)
					uni.showToast({
						title:"保存成功",
						success:res=>{
							uni.navigateBack({
								delta:1
							})
						}
					})
				}
			})
		}
	}
}
